.home {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.hero {
  text-align: center;
  margin-bottom: 4rem;
  padding: 3rem 0;
  background: linear-gradient(135deg, var(--primary-color), var(--info-color));
  color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
}

.title {
  font-size: 3rem;
  margin-bottom: 1rem;
  font-weight: 700;
}

.subtitle {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  opacity: 0.9;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.progressSummary {
  background: rgba(255, 255, 255, 0.1);
  border-radius: var(--border-radius);
  padding: 2rem;
  margin-top: 2rem;
  backdrop-filter: blur(10px);
}

.progressSummary h3 {
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.statsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1.5rem;
  max-width: 500px;
  margin: 0 auto;
}

.stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.statNumber {
  font-size: 2.5rem;
  font-weight: 700;
  color: var(--warning-color);
}

.statLabel {
  font-size: 0.9rem;
  opacity: 0.9;
}

.sectionTitle {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-align: center;
  color: var(--dark-color);
  font-weight: 700;
}

.chaptersGrid {
  margin-bottom: 4rem;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.chapterCard {
  background: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 1.5rem;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.chapterCard:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px rgba(0,0,0,0.15);
  border-color: var(--primary-color);
}

.chapterNumber {
  background: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  display: inline-block;
  margin-bottom: 1rem;
}

.chapterTitle {
  font-size: 1.3rem;
  margin-bottom: 0.8rem;
  color: var(--dark-color);
  font-weight: 600;
}

.chapterDescription {
  color: var(--secondary-color);
  margin-bottom: 1rem;
  line-height: 1.5;
}

.chapterProgress {
  border-top: 1px solid var(--border-color);
  padding-top: 1rem;
}

.progressInfo {
  display: flex;
  justify-content: space-between;
  font-size: 0.8rem;
  color: var(--secondary-color);
}

.gettingStarted {
  background: var(--light-color);
  border-radius: var(--border-radius);
  padding: 3rem;
  text-align: center;
}

.steps {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  margin: 2rem 0;
}

.step {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  text-align: right;
}

.stepNumber {
  background: var(--success-color);
  color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.2rem;
  flex-shrink: 0;
}

.stepContent h3 {
  margin-bottom: 0.5rem;
  color: var(--dark-color);
  font-size: 1.1rem;
}

.stepContent p {
  color: var(--secondary-color);
  font-size: 0.9rem;
  line-height: 1.5;
}

.startButton {
  margin-top: 2rem;
}

.ctaButton {
  background: var(--success-color);
  color: white;
  padding: 1rem 2rem;
  border-radius: var(--border-radius);
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  display: inline-block;
  transition: all 0.3s ease;
}

.ctaButton:hover {
  background: #218838;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(40, 167, 69, 0.3);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .home {
    padding: 1rem;
  }
  
  .title {
    font-size: 2rem;
  }
  
  .subtitle {
    font-size: 1.1rem;
  }
  
  .hero {
    padding: 2rem 1rem;
  }
  
  .grid {
    grid-template-columns: 1fr;
  }
  
  .steps {
    grid-template-columns: 1fr;
  }
  
  .step {
    text-align: center;
    flex-direction: column;
    align-items: center;
  }
}
